"use client";
import { useState, useEffect } from 'react';
import Link from 'next/link';

export default function ShopFixed() {
  const [products] = useState([
    {id: 1, name: 'A26', img: '/img/Design_1.jpeg', price: '₹499', category: 't-shirts'},
    {id: 2, name: 'A27', img: '/img/Design_2.jpeg', price: '₹549', category: 't-shirts'},
    {id: 3, name: 'A31', img: '/img/Design_3.jpeg', price: '₹499', category: 't-shirts'},
  ]);

  const [showOrderForm, setShowOrderForm] = useState(false);
  const [selectedProduct, setSelectedProduct] = useState(null);

  // Handle order now button click
  const handleOrderNow = (e, product) => {
    e.preventDefault();
    setSelectedProduct(product);
    setShowOrderForm(true);
  };

  // Close modal
  const closeModal = () => {
    setShowOrderForm(false);
    setSelectedProduct(null);
  };

  return (
    <div className="min-h-screen bg-gray-50 pt-20 px-4">
      {/* Navigation */}
      <div className="fixed top-0 left-0 right-0 bg-white shadow-md z-50">
        <div className="max-w-7xl mx-auto px-4 py-3 flex justify-between items-center">
          <Link href="/" className="text-2xl font-bold text-gray-800">
            Adoods
          </Link>
          <div className="flex space-x-6">
            <Link href="/" className="text-gray-700 hover:text-blue-600">
              Home
            </Link>
            <Link href="/shop-fixed" className="text-blue-600 font-medium">
              Shop
            </Link>
            <Link href="/contact" className="text-gray-700 hover:text-blue-600">
              Contact
            </Link>
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="max-w-7xl mx-auto py-10">
        <h1 className="text-3xl font-bold mb-8">Our Products</h1>
        
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {products.map((product) => (
            <div key={product.id} className="bg-white rounded-lg shadow-md overflow-hidden">
              <img 
                src={product.img} 
                alt={product.name}
                className="w-full h-64 object-cover"
              />
              <div className="p-4">
                <h3 className="text-xl font-semibold">{product.name}</h3>
                <p className="text-gray-600 mt-1">{product.price}</p>
                <button
                  onClick={(e) => handleOrderNow(e, product)}
                  className="mt-4 w-full bg-blue-600 text-white py-2 px-4 rounded hover:bg-blue-700 transition"
                >
                  Order Now
                </button>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Order Modal */}
      {showOrderForm && selectedProduct && (
        <div 
          className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50"
          onClick={closeModal}
        >
          <div 
            className="bg-white rounded-lg p-6 w-full max-w-md"
            onClick={(e) => e.stopPropagation()}
          >
            <h2 className="text-2xl font-bold mb-4">Order {selectedProduct.name}</h2>
            <form onSubmit={(e) => {
              e.preventDefault();
              alert(`Order placed for ${selectedProduct.name}`);
              closeModal();
            }}>
              <div className="mb-4">
                <label className="block text-sm font-medium mb-1">Name</label>
                <input 
                  type="text" 
                  required 
                  className="w-full p-2 border rounded"
                />
              </div>
              <div className="mb-4">
                <label className="block text-sm font-medium mb-1">Phone</label>
                <input 
                  type="tel" 
                  required 
                  className="w-full p-2 border rounded"
                />
              </div>
              <div className="flex justify-end space-x-4">
                <button 
                  type="button" 
                  onClick={closeModal}
                  className="px-4 py-2 border rounded"
                >
                  Cancel
                </button>
                <button 
                  type="submit" 
                  className="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700"
                >
                  Place Order
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}
